export default {
	data() {
		return {
			mobile: '',
			timer: null,
			time: 60,
			btn: '获取验证码'
		};
	},
	onUnload() {
		clearInterval(this.timer);
	},
	methods: {
		onInput(e){
			this.mobile=e.detail
		},
		onSubmit(e) {
			console.log('提交数据：', e.detail.value);
			this.$Fetch({
				url:"login/forget_pwd",
				data:e.detail.value,
				success:res=>{
					console.log(res)
					uni.showToast({
						title:"修改成功",
						icon:"success"
					})
					setTimeout(()=>{uni.navigateBack()},1000)
					
				}
			})
		},
		getCode() {
			if (this.time === 60) {
				console.log(this.mobile)
				this.$Fetch({
					url: 'login/code',
					data:{mobile:this.mobile},
					debug:true,
					success: res => {
						console.log(res);
						uni.showToast({
							title: '发送成功',
							icon: 'success'
						});
						this.timer = setInterval(() => {
							this.time--;
							this.btn = this.time + 's后重新获取';

							if (this.time < 0) {
								clearInterval(this.timer);
								this.timer = null;

								this.time = 60;
								this.btn = '获取验证码';
							}
						}, 1000);
					}
				});
			}
		}
	}
};
